.innerboxholder {
    width: 100vw;
    display: flex;
    align-items: center;
    height: 100%;
    flex-direction: row;
    gap: 5vw;
}

.ExperienceBox{
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 1%;
}
.exp_description {
    color: #95D7AE;
    display: flex;
    align-items: center;
    justify-content: center;
    padding:2.5%;
    font-family:'Courier New', Courier, monospace;
    font-size: 2.0rem;
}
.exp_name{
    background-color: #1b2430;
    color: #95D7AE;
    font-weight: 700;
    font-family:'Courier New', Courier;
    font-size: 2.0rem;
    width: 85%;
    height: 100%; 
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-style: solid;
    border-color: #fad4c0;
    border-radius: 5vh;
    border-width: 1vh;
    padding: 1%;
    box-sizing: border-box;
    
}