.homebackground {
    display: flex;
    background: #1b2430;
    height: 100vh;
    width: 100vw;
}

#greenline {
    position: absolute;
    display: flex;
    background-color: #95D7AE;
    width: 100vw;
    height: 6vh;
    top:50.0%;
    z-index: 10;

}
    
#greendashedline {
    display: flex;
    justify-content: center;
    font-size: 3rem;
    font-weight: 100;
    color: #95D7AE;
    position: relative;
