

nav{
    display: flex;
    align-items: center;
    background: #7EA3CC;
    justify-content: center;
    height: 10vh;
    width: 100vw;
    top:0;
    position: fixed;
    z-index: 9999;

}
#icon {
height: 10vh;
width: 7vw;
}

#navbar{
    display: flex;
    align-items: center;
    justify-content: center;

}

#navbar li {
    list-style: none;
    padding: 0 5vw;
    position: relative;
}

#navbar li a{
    text-decoration: wavy;
    color: #ffffff;
    font-size: 1.4rem;
    font-weight: 700; 
    transition: 0.4s ease-in-out; 

}

#navbar li a:hover {
    color: #FAD4C0;
    text-decoration: underline;
}
#navbar li a.active{
    color: #FAD4C0;
    text-decoration: underline;
}

#navbar li a:hover::after,
#navbar li a.active::after {
    color: #FAD4C0;
    text-decoration: underline;
}
