.contactbackground{
    background-color: #1b2430;
    height: 100vh;
    width: 100vw;
}

#contactcontent {

    display: flex;
    width: 100%;
    height: 80vh;
    bottom: 0%;
}
#portrait_container {
    display: flex;
    right: 16%;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: calc(100vw-75vw);
